.react-switch-checkbox {
    height: 0;
    width: 0;
    visibility: hidden;
  }
  
  .react-switch-label {
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    width: 80px;
    height: 40px;
    background: grey;
    border-radius: 80px;
    position: relative;
    transition: background-color .2s;
    margin-bottom: -17px;
  }
  .switch .react-switch-button {
    content: '';
    position: absolute;
    top: 2px;
    left: 2px;
    width: 35px;
    height: 35px;
    border-radius: 35px;
    transition: 0.2s;
    background: #fff;
    box-shadow: 0 0 2px 0 rgba(10, 10, 10, 0.29);
  }
  
  .react-switch-checkbox:checked + .react-switch-label .react-switch-button {
    left: calc(100% - 2px);
    transform: translateX(-100%);
    background: #35654D;
  }
  .react-switch-checkbox:not(:checked) + .react-switch-label .react-switch-button {
    left: 2px; 
    transform: translateX(0); 
    background: #580000; 
  }

  .react-switch-label:active .react-switch-button {
    width: 60px;
  }